<template>
  <div>
    <!-- height="450" -->
    <!-- 表格 -->
    <el-table
      :data="tableData"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      :header-cell-style="{ background: '#eef1f6', color: '#606266' }"
    >
      <el-table-column v-if="isShowSelect" type="selection" width="55"></el-table-column>
      <template v-for="(item, index) in tableCol">
        <el-table-column
          v-if="item.colType == 'solt'"
          :prop="item.field"
          :label="item.label"
          :width="item.width"
          :align="item.align ? item.align : 'left'"
          :key="index + 'only'"

        >
          <!-- <template slot-scope="scope" v-if="item.isShow(scope.row)"> -->
          <template slot-scope="scope" v-if="item.isShow?item.isShow(scope.row):true">
            <span
            style="display:inline-block;margin-left:5px"
            v-for="(itemOption, itemOptionIndex) in item.option"
              :key="itemOptionIndex + 'only'"
            >
               <el-button
              @click="itemOption.event(scope.row)"
              type="text"
              size="small"
              > {{ itemOption.label }} </el-button >
            </span>
         
           
          </template>
        </el-table-column>
        <el-table-column
          v-if="item.colType == 'tag'"
          :prop="item.field"
          :label="item.label"
          :width="item.width"
          :align="item.align"
          :key="index + 'only'"
          sortable

        >
         <template slot-scope="scope">
           <!-- <el-tag size="medium" :type="item.tagRender(scope.row).tagType" :style="[{width:(item.tagRender(scope.row).tagWidth?item.tagRender(scope.row).tagWidth:'70px')},{'border-radius': '15px'}]">{{item.tagRender(scope.row).tagText}}</el-tag> -->
           <el-tag size="medium" :type="item.tagRender(scope.row).tagType" :style="[{width:(item.tagRender(scope.row).tagWidth?item.tagRender(scope.row).tagWidth:'70px')}]">{{item.tagRender(scope.row).tagText}}</el-tag>
           
          </template>
        </el-table-column>
        <el-table-column
          v-if="item.colType != 'solt' && item.colType != 'tag' && item.show!=false"
          :prop="item.field"
          :label="item.label"
          :width="item.width"
          :align="item.align"
          :key="index + 'only'"
          sortable
        >

        </el-table-column>
      </template>
    </el-table>
    <!-- 分页 -->
    <div class="pagination-wrap" v-if="ispagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="listQuery.page"
        :page-sizes="[5, 10, 20, 30, 40, 50, 100]"
        :page-size="listQuery.page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listQuery.total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "ELTable",
  props: {
    // 表格列
    tableCol: {
      type: Array,
      default: () => [],
    },
    // 表格数据
    tableData: {
      type: Array,
      default: () => [],
    },
    ispagination: {
      type: Boolean, // 分页是否显示
      default: true,
    },
    isShowSelect: {
      type: Boolean, // 分页是否显示
      default: false,
    },
    listQuery: {
      type: Object,
      default: function () {
        return {
          page: 1,
          page_size: 10,
          total: 0,
        };
      },
    },
  },
  data() {
    return {
    };
  },
  methods: {
    handleSizeChange(val) {
      this.$emit("sizeChange", val);
    },
    handleCurrentChange(val) {
      this.$emit("currentChange", val);
    },
     handleSelectionChange(val) {
      this.$emit("handleSelectionChange", val);
    }
  },
};
</script>
<style lang="scss" scoped>
</style>