<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="数据库名称">
          <el-input
            v-model="search_database_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="物料类型">
          <el-select v-model="search_database_type" placeholder="请选择">
            <el-option label="全部" value="全部"></el-option>
            <el-option label="MySQL" value="MySQL"></el-option>
            <el-option label="SQLServer" value="SQLServer"></el-option>
            <el-option label="Oracle" value="Oracle"></el-option>
          </el-select>
        </el-form-item>
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>人员列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable :tableCol="tableCol" :tableData="tableData"> </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
     
        <el-form-item label="数据库名称" prop="db_name">
          <el-input v-model="dialogForm.db_name"></el-input>
        </el-form-item>
        <el-form-item label="数据库类型" prop="db_type">
           <el-select
            v-model="dialogForm.db_type"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="全部" value="全部"></el-option>
            <el-option label="MySQL" value="MySQL"></el-option>
            <el-option label="SQLServer" value="SQLServer"></el-option>
            <el-option label="Oracle" value="Oracle"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="db_status">
          <el-select
            v-model="dialogForm.db_status"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="启用" value="启用"></el-option>
            <el-option label="禁用" value="禁用"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="db_description">
           <el-input type="textarea" :rows="3" v-model="dialogForm.db_description"></el-input>
        </el-form-item>
      
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
export default {
  name: "Database",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      search_database_name: "",
      search_database_type: "",
      // 新增人员弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        db_code: "",
        db_name: "",
        db_type: "",
        db_status: "",
        db_description: "",
      },
      dialogFormBase: {
        db_code: "",
        db_name: "",
        db_type: "",
        db_status: "",
        db_description: "",
      },
      dialogFormRules: {
        db_name: [{ required: true, message: "请输入数据库名称", trigger: "blur" }],
        db_type: [
          { required: true, message: "请选择数据库类型", trigger: "blur" },
        ],
        db_status: [
          { required: true, message: "请选择状态", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "数据库名称",
          field: "db_name",
          align: "center",
        },
        {
          label: "数据库类型",
          field: "db_type",
          align: "center",
        },
        {
          label: "状态",
          field: "db_status",
          align: "center",
        },
        {
          label: "备注",
          field: "db_description",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  methods: {
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      console.log("submit!");
    },
    // 重置
    resetForm() {
      this.user_id = "";
    },
    // 新增
    addBtn() {
      this.dialogTitle = "新增数据库";
      this.dialogFlag = true;
    },
    // 弹出层确定
    dialogSave() {
       this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>

</style>